import React, { useState, useEffect } from 'react';
import {Link} from 'react-router-dom'

function Intro() {
  const [state, setState] = useState({button:false});

  useEffect(()=>{
    document.getElementById('video').play();
    document.getElementById("video").addEventListener("ended",enableButton)
  },[])

  const enableButton=()=>{
    setState({button:true})
  }

  return (
        <div className="intro">
          <div>
            <video id="video" className="videoplayer" width="100%" controls>
                <source src="http://augmo.net/videos/tionkar1.mp4" type="video/mp4"/>
            </video>
          </div>
          {state.button?
          <div className="button_wrap">
              <Link to="/" className="prev_button">Back</Link>
              <Link to="/details" className="next_button">Next</Link>
          </div>:null
          }
        </div>
  );
}

export default Intro;
