import React, { useState } from 'react';
import {Link} from 'react-router-dom'
import Progress from './progress'
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';

function Details(props) {
  const [state, setState] = useState({});

  const handleChange=(event)=>{
    setState({...state,[event.target.name]:event.target.value})
  }

  const handleNext=()=>{
      if(state.first_name === undefined && state.last_name === undefined && state.location === undefined && state.department === undefined){
          toast.error("Please fill all the details")
      }else{
          props.history.push('/survey/1')
      }
  }

  return (
        <div className="details">
        <div className="survey_title">
            Question 1
        </div>
          <div className="details_title">Enter your details</div>
            <div className="form">
            <div className="form-wrap">
                <div className="form-item">
                    <input className="form-item-input" autocomplete="off" type='text' name="first_name"  placeholder="First Name" defalutValue={state.first_name} onChange={(event)=>handleChange(event)}/>
                </div>
                <div className="form-item">
                    <input className="form-item-input" autocomplete="off" type='text' name="last_name" placeholder="Last Name" defalutValue={state.last_name} onChange={(event)=>handleChange(event)}/>
                </div>
            </div>
            <div className="form-wrap">
                <div className="form-item">
                    <select id="xyz" className="form-item-select" name="location" onChange={(event)=>handleChange(event)}>
                        <option id="option" value="">Select Location</option>
                        <option id="option" value="Warsaw">Warsaw</option>
                        <option id="option" value="Pozhan">Pozhan</option>
                        <option id="option" value="Rybnik">Rybnik</option>
                        <option id="option" value="Pinewood">Pinewood</option>
                        <option id="option" value="Tychy">Tychy</option>
                    </select>
                </div>
                <div className="form-item">
                    <select className="form-item-select" name="department" onChange={(event)=>handleChange(event)}>
                        <option id="option" value="">Select Department</option>
                        <option id="option" value="Sales">Sales</option>
                        <option id="option" value="Service">Service</option>
                        <option id="option" value="Parts">Parts</option>
                        <option id="option" value="HQ">HQ</option>
                    </select>
                </div>
            </div>
                <div className="button_wrap">
                <Link to="/intro" className="prev_button">Back</Link>
                <div onClick={()=>handleNext()} className="next_button">Next</div>
            </div>
          </div>
          <Progress id={1}/>
          <ToastContainer autoClose={3000} />
        </div>
  );
}

export default Details;
