import React from 'react';
import P7 from '../assets/images/P7 Logo.png'
import Porche from '../assets/images/Porche.png'
import BeEpic from '../assets/images/Be Epic Logo.png'

function NavBar() {
  return (
        <div className="navbar">
          <div className="navbar-logos">
            <div><img className="logo" src={Porche} alt="Porche Logo" /></div>
            <div><img className="logo" src={P7} alt="P7 Logo" /></div>
            <div><img className="logo" src={BeEpic} alt="Be Epic Logo" /></div>
          </div>
          <div>
            <div className="navbar_title">Manager Survey</div>
          </div>
        </div>
  );
}

export default NavBar;
