import React from 'react';
import {questions} from './sample'

let data = Object.values(questions).filter((value,index)=>value.question_no>=0)

function Home(props) {
  return (
    <div className="progress_container">
        {
            Object.values(data).map((value,index)=>
                <div key={index} style={{background:value.question_no===props.id?"orange":"transparent"}} className="progress_dot"></div>
            )
        }
    </div>
  );
}

export default Home;
