import React, { useState, useRef, createRef } from 'react';
import {Link} from 'react-router-dom'
import {questions} from './sample'
import Tick from "../assets/images/checked.svg"
import Progress from './progress'
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';


function Survey({match,history}) {
  const [state, setState] = useState(questions);
  let id = parseInt(match.params.id,10)
  let array;
  if(state[id].radio_content){
    array = state[id].radio_content
  }else{
      array = []
  }
  let scrollToDiv = useRef(array.map(() => createRef()));
  const handleDotClick=(index)=>{
    state[id].answer = index;
    setState([...state])
  }

  const handleRadioDotClick=(index, radioIndex)=>{
    state[parseInt(id)+1].answer[radioIndex] = index;
    setState([...state])
  }

  const handleChange=(event)=>{
    state[id].text_answer = event.target.value;
    setState([...state])
  }

  const handleBoolean=(boolean)=>{
    state[id].answer = boolean;
    setState([...state])
  }

  const handleRadio=(index)=>{
      if(state[id].answer.indexOf(index)!== -1){
        state[id].answer = state[id].answer.filter(e => e !== index)
    }else{
        state[id].answer.push(index)
    }
    setState([...state])
    // scrollToDiv.current[index].current.scrollIntoView({ behavior: 'smooth', block: 'start' })
    console.log(scrollToDiv.current[index].current.offsetTop)
    window.scroll({left:0,top: scrollToDiv.current[index].current.offsetTop ,behavior: 'smooth' })

  }

  const handleCheckbox=(index)=>{
    if(state[id].answer.indexOf(index)!== -1){
        state[id].answer = state[id].answer.filter(e => e !== index)
    }else{
        state[id].answer.push(index)
    }
    setState([...state])
  }

  const handleNext=()=>{
      if(state[id].answer === undefined){
        toast.error("Please answer to continue")
      }else if(state[id].answer.length===0){
          toast.error("Please answer to continue")
      }else{
        if(questions.length-1===parseInt(id)){
            history.push('/thankyou')
        }else{
            if(state[id].boolean_content){
                if(state[id].boolean_content[state[id].answer]==="No"){
                    history.push(`/survey/${parseInt(id,10)+2}`)
                }else{
                    history.push(`/survey/${parseInt(id,10)+1}`)
                }
            }else if(state[id].skip_next){
                history.push(`/survey/${parseInt(id,10)+2}`)
            }else{
                history.push(`/survey/${parseInt(id,10)+1}`)
            }

        }
      }
  }

  const handleBack = () =>{
    if(1===parseInt(id)){
        history.push('/details')
    }else{
        if(state[id].skip_prev){
            history.push(`/survey/${parseInt(id,10)-2}`)
        }else{
            history.push(`/survey/${parseInt(id,10)-1}`)
        }

    }
  }

  const handleEmoticon=(value)=>{
    state[id].emoticon_answer = value;
    setState([...state])
  }

  const Slider =(props) =>{
      return(
        <div id={"slider_container"+props.index} className="slider_container">
        <div className="slider_line"></div>
        <div className="slider_wrap">
            {
                props.question.slide_content.map((value,index)=>
                <div key={index} className="slider_dots" onClick={()=>handleRadioDotClick(index,props.index)}>
                {
                    props.question.answer[props.index] === index?
                    <div className="slider_dot_selected"></div>:null
                }
                    <div className="slider_dot_text">{value}</div>
                </div>
            )}
        </div>
    </div>
      )
  }


  console.log(state[id])
  return (
        <div className="survey animate">
        <div className="survey_title">
        {
            state[id].question_title?
                <div className="question_title">{state[id].question_title}</div>:null
        }

        {    state[id].title?
                <div className="title">{state[id].title}</div>:null
        }
            </div>

        {state[id].final_question?
            <div className="emoticon_container">
            {    state[id].final_question?
                <div className="emoticon_question_title">{state[id].final_question}</div>:null
            }
            <div className="emoticon_wrapper">
                {state[id].emoticon_content.map((value,index)=>
                    <div style={{background:value===state[id].emoticon_answer?"rgba(232,175,0,0.6)":null}} key={index} onClick={()=>handleEmoticon(value)} className="emoticon_wrap">
                        <img src={value} alt="emoticon" width="100px" height="100px"/>
                    </div>
                )}
            </div>

            </div>:null
            }


            <div className="survey_question_title">

                {state[id].question}

            </div>

            {
            state[id].question_info?
            <div className="survey_question_info">
                {state[id].question_info}
            </div>:null
        }

        {
            state[id].choosen_option!==undefined?
            <div className="radio_container">
                <div className="radio_wrap">
                    <div className="radio_dots_wrap">

                        <div className="radio_dots">
                            <div className="boolean_dot_selected"></div>
                        </div>
                        <div className="radio_dot_text">{state[id].radio_content[state[id].choosen_option]}</div>
                    </div>
                </div>
            </div>:null
        }

            {state[id].type.indexOf('checkbox')!==-1?
                <div className="checkbox_container">
                    <div className="checkbox_line"></div>
                    <div className="checkbox_wrap">
                        {
                            state[id].checkbox_content.map((value,index)=>
                            <div key={index} className="checkbox_dots" onClick={()=>handleCheckbox(index)}>
                            {
                                state[id].answer.indexOf(index) !== -1?
                                <div className="checkbox_dot_selected"><img src={Tick} alt="checked" width="20px"/></div>:null
                            }
                                <div className="checkbox_dot_text">{value}</div>
                            </div>
                        )}
                    </div>
                </div>:null
            }

            {state[id].type.indexOf('radio')!==-1?
                <div className="radio_container">
                    <div className="radio_line"></div>
                    <div className="radio_wrap">
                        {
                            state[id].radio_content.map((value,index)=>
                            <div key={index}>
                                <div ref={scrollToDiv.current[index]} id={index} onClick={()=>handleRadio(index)} className="radio_dots_wrap" key={index}>

                                <div className="radio_dots">
                                    {
                                    state[id].answer.indexOf(index)!== -1?
                                    <div className="radio_dot_selected"></div>:null
                                    }
                                </div>
                                <div className="radio_dot_text">{value}</div>
                                </div>
                                {state[id].skip_next?
                                    state[id].answer.indexOf(index)!== -1 ?state[id].radio_content.length-1 === index && state[id].radio_content[Math.max(...state[id].answer)]==="Other"?null:
                                    <div >
                                    <Slider index={index} question={state[id+1]}/>
                                    </div>:null:null
                                }
                            </div>
                        )}
                    </div>
                </div>:null
            }

            {state[id].type.indexOf('slider')!==-1?
                <div className="slider_container">
                    <div className="slider_line"></div>
                    <div className="slider_wrap">
                        {
                            state[id].slide_content.map((value,index)=>
                            <div key={index} className="slider_dots" onClick={()=>handleDotClick(index)}>
                            {
                                state[id].answer === index?
                                <div className="slider_dot_selected"></div>:null
                            }
                                <div className="slider_dot_text">{value}</div>
                            </div>
                        )}
                    </div>
                </div>:null
            }

            {state[id].type.indexOf('text')!==-1?
            <div className="textbox_container">
            <textarea className="textbox_input" name={id}  placeholder="Please give details for others..." onChange={(event)=>handleChange(event)}></textarea>
            </div>:null
            }

            {state[id].radio_content?
            state[id].radio_content[Math.max(...state[id].answer)]==="Other"?
            <div className="textbox_container">
            <textarea className="textbox_input" name={id}  placeholder="Please give details for others..." onChange={(event)=>handleChange(event)}></textarea>
            </div>:null:null
            }

            {state[id].type.indexOf('boolean')!==-1?
            <div className="boolean_container">
            {state[id].boolean_content.map((value,index)=>
                <div key={index} onClick={()=>handleBoolean(index)} className="boolean_wrap">
                    <div className="boolean_radio">
                    {
                        state[id].answer === index?
                        <div className="boolean_dot_selected"></div>:null
                    }
                    </div>
                    <div className="boolean_text">{value}</div>
                </div>
            )}
            </div>:null
            }

            <div className="button_wrap">
                <Link  onClick={()=>handleBack()} className="prev_button">Back</Link>
                <div onClick={()=>handleNext()} className="next_button">{state.length-1===id?"Finish":"Next"}</div>
            </div>
            <Progress id={state[id].question_no}/>
            <ToastContainer autoClose={3000} />

        </div>
  );
}

export default Survey;
